<template>
  <div
  ref="section"
  :style="{width: sectionWidth + 'px', height: sectionHeight + 'px'}"
  class="cat-builder__section"
  @dragenter="sectionEnter"
  @mouseenter="mouseenterHandler"
  @mouseleave="mouseleaveHandler">
    <header @dragenter="headerDragenter" @dragleave="headerDragleave" @dragover="headerDragover" @drop.prevent.stop="headerDrop" :class="sectionClasses"><span>{{section.name}}</span></header>
    <div ref="body" class="section__body" @dragover="bodyDragover" @drop="bodyDrop" @scroll="checkIfMoreItemsNeeded">
      <div class="section__drop-target" :class="targetClasses" ref="target" @transitionend="targetTransitionEnded" @drop.prevent.stop="targetDrop" @dragover.prevent="targetDragover"></div>
      <tree-node v-for="(cat, index) in section.categories"
      :cat="cat"
      :parent-guid="section.guid"
      :cat-index="index"
      :key="cat.guid"
      @selected="$emit('nodeSelected', $event)"
      @nodeDropped="nodeDropped"
      @targetBefore="showDropzone"
      @targetAfter="showDropzone"
      @hideTarget="setTargetVisible(false)"
      ref="node" />
      <div class="section__remaining-items-placeholder" :style="remainingItemsStyle" ref="remaining"></div>
    </div>
    <transition name="fade">
      <create-node v-show="showCreateTriggerComp" ref="creator" :nodeType="sectionNodeType" :parentInfo="createPayload" @nodeCreated="$emit('nodeCreated', $event)" />
    </transition>
  </div>
</template>

<script>
import * as nodeTypes from '../../js/tree-node-types'
import { createNamespacedHelpers } from 'vuex'
import TreeNode from './tree-node.vue'
import CreateNode from './create-node.vue'
const { mapGetters, mapMutations } = createNamespacedHelpers('builder')
export default {
  name: 'tree-section',
  data () {
    return {
      targetVisible: false,
      targetTransitioning: false,
      targetIndex: 0,
      headerEnterTimer: null,
      showCreateTrigger: false
    }
  },
  props: {
    section: Object
  },
  computed: {
    ...mapGetters(['sectionWidth', 'sectionHeight', 'dragInfo', 'selectedCatalog', 'displayedNodePosition']),
    targetClasses () {
      return {
        show: this.showTarget,
        transitions: this.dragInfo.dragging
      }
    },
    remainingItemsStyle () {
      const rowHeight = (!this.section.categories.length || !this.section.categoriesCount || this.section.endOfList) ? 0 : 35
      const placeholderHeight = rowHeight * (this.section.categoriesCount - this.section.categories.length)
      return { height: `${placeholderHeight}px` }
    },
    sectionClasses () {
      return {
        'no-drop': !this.canDrop
      }
    },
    canDrop () {
      if (!this.dragInfo.dragging) return false
      if (this.dragInfo.node.guid === this.section.guid) return false
      return true
    },
    isSectionOfOrigin () {
      return this.dragInfo.parentGuid === this.section.guid
    },
    draggingCategoryIntoCatalogSection () {
      if (!this.dragInfo.dragging) return
      return (this.section.position === 0 && this.dragInfo.node.nodeType === nodeTypes.category)
    },
    showTarget () {
      return this.targetVisible && this.canDrop && this.section.guid === this.dragInfo.currentSectionGuid
    },
    sectionNodeType () {
      return this.section.position === 0 ? nodeTypes.catalog : nodeTypes.category
    },
    createPayload () {
      const payload = {}
      if (!this.selectedCatalog) return payload
      if (this.sectionNodeType !== nodeTypes.store) {
        payload.parentCategoryId = this.section.guid === this.selectedCatalog.guid ? null : this.section.guid
        payload.productCatalogId = this.selectedCatalog.guid
        payload.parentGuid = this.section.guid
      }
      return payload
    },
    showCreateTriggerComp () {
      return this.showCreateTrigger && !this.dragInfo.dragging
    },
    createVisible () {
      return this.$refs.creator.inputVisible
    }
  },
  methods: {
    ...mapMutations(['setDragSectionGuid']),

    showCreate () {
      if (this.createVisible) return
      this.showCreateTrigger = true
      this.$refs.creator.showInput()
    },

    hideCreate () {
      if (!this.createVisible) return
      this.$refs.creator.hideInput()
      this.showCreateTrigger = false
    },

    mouseenterHandler () {
      this.showCreateTrigger = true
    },

    mouseleaveHandler () {
      if (this.$refs.creator.inputVisible) return
      this.showCreateTrigger = false
      this.$refs.creator.hideInput(false)
    },

    sectionEnter () {
      this.setDragSectionGuid(this.section.guid)
    },

    bodyDragover (e) {
      if (this.canDrop) {
        e.preventDefault()
        e.dataTransfer.dropEffect = 'move'
      }
    },

    bodyDrop (e) {
      if (this.canDrop) {
        e.preventDefault()
        const newIndex = this.section.categories.length
        const sortOrder = newIndex ? this.section.categories[newIndex - 1].sortOrder + 1 : 0
        this.dropNode(sortOrder, newIndex)
      }
    },

    targetTransitionEnded (e) {
      if (e.propertyName === 'height') {
        this.targetTransitioning = false
      }
    },

    targetDrop () {
      let newIndex = this.targetIndex
      if (this.isSectionOfOrigin && this.dragInfo.catIndex < this.targetIndex) {
        newIndex = this.targetIndex - 1
      }
      const nextItem = this.section.categories[newIndex]
      const sortOrder = nextItem ? nextItem.sortOrder : this.section.categories[this.section.categories.length - 1].sortOrder + 1
      this.dropNode(sortOrder, newIndex)
    },

    targetDragover (e) {
      e.dataTransfer.dropEffect = 'move'
    },

    headerDragenter (e) {
      if (this.canDrop && !this.draggingCategoryIntoCatalogSection) {
        this.headerEnterTimer = window.setTimeout(() => {
          e.target.classList.add('can-drop')
          this.setTargetVisible(false)
          this.headerEnterTimer = undefined
        }, 500)
      }
    },

    headerDragleave (e) {
      if (this.headerEnterTimer) {
        window.clearTimeout(this.headerEnterTimer)
        this.headerEnterTimer = undefined
      }
      e.target.classList.remove('can-drop')
    },

    headerDragover (e) {
      if (this.canDrop && !this.draggingCategoryIntoCatalogSection) {
        e.preventDefault()
        e.dataTransfer.dropEffect = 'move'
      }
    },

    headerDrop () {
      this.dropNode(null, -1)
    },

    showDropzone (index) {
      if (this.draggingCategoryIntoCatalogSection) return
      this.shiftTargetPosition(index)
    },

    setTargetVisible (visible) {
      const visibility = this.canDrop ? visible : false
      window.requestAnimationFrame(() => {
        if (this.targetTransitioning) {
          this.setTargetVisible(visibility)
          return
        }
        this.targetTransitioning = this.targetVisible !== visibility
        this.targetVisible = visibility
      })
    },

    shiftTargetPosition (index) {
      if (this.targetTransitioning) {
        window.setTimeout(() => {
          this.shiftTargetPosition(index)
        }, 32)
        return
      }
      if (this.targetIsNextToDraggedItem(index)) {
        this.setTargetVisible(false)
        return
      }

      if (this.targetIndex !== index) {
        const nodeBefore = this.$refs.node[index] ? this.$refs.node[index].$el : null
        this.$refs.body.insertBefore(this.$refs.target, nodeBefore)
        for (let i = 0; i < this.$refs.body.children.length; i++) {
          if (this.$refs.target === this.$refs.body.children[i]) {
            this.targetIndex = i
          }
        }
      }
      this.setTargetVisible(true) // target already at the correct position
    },

    targetIsNextToDraggedItem (index) {
      if (this.section.categories[index] && this.section.categories[index].guid === this.dragInfo.node.guid) {
        return true
      }
      if (this.section.categories[index - 1] && this.section.categories[index - 1].guid === this.dragInfo.node.guid) {
        return true
      }
      return false
    },

    dropNode (sortOrder, newIndex) {
      const moveTo = {
        parentGuid: this.section.guid,
        sortOrder: sortOrder,
        newIndex: newIndex
      }
      this.$emit('nodeDropped', moveTo)
    },

    nodeDropped (moveTo) {
      this.$emit('nodeDropped', moveTo)
    },

    resetScroll () {
      this.$refs.body.scrollTop = 0
    },

    getScrollPosition () {
      return this.$refs.body.scrollTop
    },

    setScrollPosition (scrollTop) {
      this.$refs.body.scrollTop = scrollTop
    },

    checkIfMoreItemsNeeded () {
      const remainingItemsPlaceholder = this.$refs.remaining
      const panel = this.$refs.body
      if (!remainingItemsPlaceholder || !panel) {
        return
      }
      const placeholderTop = remainingItemsPlaceholder.getBoundingClientRect().top
      const placeholderIsInViewOrNearby = placeholderTop < 2 * panel.offsetHeight
      if (placeholderIsInViewOrNearby) {
        this.$emit('needsMoreCats', this.section)
      }
    }
  },
  components: {
    TreeNode,
    CreateNode
  }
}
</script>
<style lang="stylus">
  .cat-builder__section
    border-right solid 1px $col_light_gray
    display inline-block
    transition width 200ms ease
    vertical-align top
    position relative
    header
      background-color $col_table_header_bg
      font-size 0.75rem
      padding 0 10px
      border-bottom solid 1px $col_light_gray
      height $header_height
      display flex
      align-items center
      overflow hidden
      transition all 300ms ease-in-out
      &.can-drop
        border dashed 1px rgba(0,0,0,0.5)
      span
        max-width 95%
        truncate()
    &.no-drop
      opacity 0.5

  .section__body
    width 100%
    height "calc(100% - %s)" % $header_height
    overflow-y auto
    overflow-x hidden
    padding-bottom 37px

  .section__drop-target
    border none
    height 0
    opacity 0
    &.transitions
      transition all 100ms ease-out
    &.show
      opacity 1
      height 35px
      border dashed 1px rgba(0,0,0,0.3)

  .section__remaining-items-placeholder
    background url(../../../shared/assets/placeholder.gif) repeat-y
</style>
